.TodoMain {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0;
}

.TodoMainIcon {
    margin-left: auto;
    font-size: 0.8rem;
    width: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: transparent;
}

.TodoMain:hover .TodoMainIcon {
    color: black;
}

.TodoMainCheckbox {
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.TodoMainCheckbox:hover {
    cursor: pointer;
    transform: scale(1.02);
    overflow-y: scroll;
}

.TodoMainCheckbox input[type='text'] {
    padding: 5px;
    height: 15px;
    background-color: white;
    color: black;
    border-bottom: 1px solid black;
    border-radius: 0px;
}

input[type=checkbox]:checked+label {
    color: red;
    text-decoration: line-through;
}


/*This is the MDN spec for doing this*/