* {
    margin: 0;
    padding: 0;
    font-family: inherit;
    letter-spacing: 1px;
}

::-webkit-scrollbar {
    width: 5px;
}


/* Track */

::-webkit-scrollbar-track {
    -webkit-border-radius: 8px;
    border-radius: 8px;
}


/* Handle */

::-webkit-scrollbar-thumb {
    -webkit-border-radius: 8px;
    border-radius: 8px;
    background: transparent;
}

::-webkit-scrollbar-thumb:window-inactive {
    background: transparent;
}