.Logo {
    padding: 0.5rem;
    margin-right: 20px;
    font-family: 'Yellowtail', cursive;
}

.Logo div {
    font-weight: 1000;
    font-size: 100;
    text-align: center;
    width: 110px;
    height: 110px;
    border: 1px solid black;
    border-radius: 50%;
    line-height: 100px;
    margin-left: 45px;
    font-family: 'Yellowtail', cursive;
    color: black;
}

.Logo:hover {
    cursor: pointer;
    transform: scale(1.2);
}

@media(max-width: 650px) {
    .Logo {
        display: none;
    }
}