    .text,
    .email,
    .password.w3lpass {
        width: 94%;
    }
}

@media(max-width:1366px) {
    h2 {
        font-size: 2.6em;
    }
    .agileits-top {
        padding: 2.5em;
        position: relative;
        top: 16px;
    }
    .main-agileinfo {
        margin: 2em auto;
    }
    .main-agileinfo {
        width: 36%;
    }
}

@media(max-width:1280px) {
    .main-agileinfo {
        width: 40%;
    }
}

@media(max-width:1080px) {
    .main-agileinfo {
        width: 46%;
    }
}

@media(max-width:1024px) {
