    }
    .main-agileinfo {
        width: 1000px;
        position: relative;
        top: 70px;
    }
    .agileits-top {
        margin: 30px;
    }
}

@media(max-width:736px) {
    .main-agileinfo {
        width: 62%;
    }
}

@media(max-width:667px) {
    .main-agileinfo {
        width: 67%;
