        letter-spacing: 3px;
    }
}

@media(max-width:568px) {
    .main-agileinfo {
        width: 75%;
    }
    .colorlibcopy-agile p {
        padding: 0 2em;
    }
}

@media(max-width:480px) {
    h2 {
        font-size: 1.8em;
        letter-spacing: 3px;
    }
    .agileits-top {
        padding: 1.8em;
        position: relative;
        top: 16px;
    }
    .text,
    .text.email,
    .password.w3lpass {
        width: 91%;
    }
    .agileits-top p {
        font-size: 0.9em;
    }
}

@media(max-width:414px) {
    h2 {
        font-size: 1.8em;
        letter-spacing: 2px;
    }
    .main-agileinfo {
        width: 85%;
        margin: 1.5em auto;
    }
    .text:focus,
    .text:valid {
        background-position: 0 0px;
    }
    .wthree-text ul li,
    x .wthree-text ul li:nth-child(2) {
        display: block;
        float: none;
    }
    .wthree-text ul li:nth-child(2) {
        margin-top: 1.5em;
    }
    .RegisterButton {
        margin: 2em 0 1.5em;
        letter-spacing: 3px;
    }
    .RegisterButton {
        margin: 2em 0 1.5em;
    }
    .colorlibcopy-agile {
        margin: 1em 0 1em;
    }
}

@media(max-width:384px) {
    .main-agileinfo {
        width: 88%;
    }
    .colorlibcopy-agile {
        padding: 0 1em;
    }
}

@media(max-width:375px) {
    .agileits-top {
        letter-spacing: 0px;
        position: relative;
        top: 16px;
    }
}

@media(max-width:320px) {
    .main-w3layouts {
        padding: 1.5em 0 0;
    }
    .agileits-top {
        padding: 1.2em;
        position: relative;
        top: 16px;
    }
    .colorlibcopy-agile {
        margin: 0 0 1em;
    }
    .text,
    .email,
    .password.w3lpass {
        width: 89.5%;
        font-size: 0.85em;
    }
    h2 {
        font-size: 1.7em;
        letter-spacing: 0px;
    }
    .main-agileinfo {
        width: 92%;
        margin: 1em auto;
    }
    .text:focus,
    .text:valid {
        background-position: 0 0px;
    }
    Button {
        margin: 1.5em 0;
        padding: 0.8em;
        font-size: .85em;
    }
    .colorlibcopy-agile p {
        font-size: .85em;
    }
    .wthree-text label {
        font-size: 0.85em;
    }
    .main-w3layouts {
        padding: 1em 0 0;
    }
}

.text,
.email,
.w3lpass {
    font-size: 0.9em;
    color: #000;
    font-weight: 100;
    width: 94.5%;
    display: block;
    border: none;
    padding: 0.8em;
    border: solid 1px rgba(255, 255, 255, 0.37);
    -webkit-transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
    transition: all 0.3s cubic-bezier(0.64, 0.09, 0.08, 1);
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 96%, #000 4%);
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, #000 4%);
    background-position: -800px 0;
    background-size: 100%;
    background-repeat: no-repeat;
    font-family: 'Roboto', sans-serif;
}

.email,
.text.w3lpass {
    margin: 2em 0;
}

.text:focus,
.text:valid {
    box-shadow: none;
    outline: none;
    background-position: 0 0;
}

.text:focus::-webkit-input-placeholder,
.text:valid::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.7);
    font-size: .9em;
    -webkit-transform: translateY(-30px);
    -moz-transform: translateY(-30px);
    -o-transform: translateY(-30px);
    -ms-transform: translateY(-30px);
    transform: translateY(-30px);
    visibility: visible !important;
}